import React from "react"
import { Link } from "gatsby"
import Img from "gatsby-image"
import Like from "../assets/svg/like.svg"
import Comment from "../assets/svg/comment.svg"
import Calendar from "../assets/svg/calendar.svg"

const BlogCard = ({ post }) => (
  <div className="blog-card">
    <div className="card">
      <Img
        className="card-image"
        fluid={post.frontmatter.image.childImageSharp.fluid}
      />
      <div className="card-content">
        <div className="title-content">
          <h3>
            <Link key={post.id} to={post.frontmatter.path}>
              {post.frontmatter.title}
            </Link>
          </h3>
          <div className="intro">
            {post.frontmatter.tags !== null
              ? post.frontmatter.tags.map(tag => {
                  return (
                    <Link
                      key={tag}
                      to={`tags/${tag.toLowerCase()}`}
                      className="intro-tag"
                    >
                      {tag}
                    </Link>
                  )
                })
              : null}
          </div>
        </div>
        <div className="info">
          <p>
            Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim
            ad minim...
          </p>
          <Link to={post.frontmatter.path} className="info-more">
            Read Article
          </Link>
        </div>
        <div className="utility-info">
          <ul className="utility-list">
            <li>
              <span>
                <Like /> <p className="utility-list-elem">#</p>
              </span>
            </li>
            <li>
              <span>
                <Comment /> <p className="utility-list-elem">#</p>
              </span>
            </li>
            <li>
              <span>
                <Calendar />
                <p className="utility-list-elem">{post.frontmatter.date}</p>
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
)

export default BlogCard
