import React from "react"
import { StaticQuery, graphql, Link } from "gatsby"

const SubHeader = () => (
  <StaticQuery
    query={subHeaderQuery}
    render={data => {
      const { subHeader } = data.site.siteMetadata

      return (
        <header className="sub-header">
          <nav>
            <ul>
              {subHeader.map(item => {
                const { link, cod, name } = item

                return (
                  <li className="category" key={cod}>
                    <Link className="category-link" to={link}>
                      {name}
                    </Link>
                  </li>
                )
              })}
            </ul>
          </nav>
        </header>
      )
    }}
  />
)

const subHeaderQuery = graphql`
  query {
    site {
      siteMetadata {
        subHeader {
          cod
          link
          name
        }
      }
    }
  }
`

export default SubHeader
