import React from "react"
import { Link } from "gatsby"

const PaginationLinks = ({ currentPage, numPages }) => {
  const isFirst = currentPage === 1
  const isLast = currentPage === numPages
  const prevPage = currentPage - 1 === 1 ? "/" : (currentPage - 1).toString()
  const nextPage = (currentPage + 1).toString()

  return (
    <div className="pagination">
      <div className="prev">
        <Link to={prevPage} rel="prev" className={isFirst ? "desable" : ""}>
          ← <span>Newer Posts</span>
        </Link>
      </div>
      <div className="num">
        {Array.from({ length: numPages }, (_, i) =>
          generateNumbers(i, numPages, currentPage)
        )}
      </div>
      <div className="next">
        <Link to={nextPage} rel="next" className={isLast ? "desable" : ""}>
          <span>Older Posts</span> →
        </Link>
      </div>
    </div>
  )
}

const LinkNums = (index, currentPage, elem) => (
  <Link
    className="number-page"
    key={`pagination-number${index + 1}`}
    to={`/${index === 0 ? "" : index + 1}`}
  >
    <p className={currentPage === index + 1 ? "zoom active" : "zoom"}>{elem}</p>
  </Link>
)

const generateNumbers = (index, numPages, currentPage) => {
  const num = index + 1

  if (index === 0 || index === numPages - 1 || numPages <= 5) {
    return LinkNums(index, currentPage, num)
  } else {
    if (
      (currentPage === 1 &&
        (currentPage + 1 === num || currentPage + 2 === num)) ||
      (currentPage === numPages &&
        (currentPage - 1 === num || currentPage - 2 === num)) ||
      ((currentPage !== 1 || currentPage !== numPages) &&
        (currentPage - 1 === num ||
          currentPage === num ||
          currentPage + 1 === num))
    ) {
      return LinkNums(index, currentPage, num)
    }

    if (
      (currentPage === 1 && currentPage + 3 === num) ||
      (currentPage === numPages && currentPage - 3 === num) ||
      ((currentPage !== 1 || currentPage !== numPages) &&
        (currentPage - 2 === num ||
          currentPage === num ||
          currentPage + 2 === num))
    ) {
      return LinkNums(index, currentPage, "...")
    }
  }
}

export default PaginationLinks
